	<template>
	<main>
		<h1>find{{this.$store.state.count}}</h1>
		<button @click="add"></button>
		<p>{{this.$store.getters.getNum}}</p>
	</main>

	</template>
	<script>
		export default{
			methods:{
				add(){

					this.$store.commit('jian',-2)
				}
			}
		}
	</script>


