import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
	// statedata
	//  this.$store.state.
	// this.$store.state.count++mutations
  state: {
    count: 0,
    num:'hello',
    weather:''
  },
  	// mutationsmethod
  	// this.$store.commit('',)
  mutations: {
    increment (state,a) {
      state.count+=a
    },
    jian(state){
    	state.count--
    },
    add(a){
      console.log(a)
    }
  },

  // this.$store.getters.
  getters:{
    getNum(state){
      return ''+state.count+''
    }
  }

})


